
#topbar{
  background:#ffffff;
  box-shadow:0 1px 3px 0 rgba(0,0,0,0.25);
}
#topbar>.wrapper{
  min-width: 1024px;
  max-width: 1440px;
  margin: 0 auto;
  height:64px;
  padding: 0 16px;
}
#topbar>.wrapper{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#topbar .logo{
  font-size:24px;
  color:#000000;
}  



